import React, { useEffect } from 'react';
import { Grid, Box, Typography } from '@mui/material';
import ScrollReveal from 'scrollreveal'; // Importing ScrollReveal

const MessageComponent = () => {
  useEffect(() => {
    // Apply ScrollReveal to different sections
    ScrollReveal().reveal('.reveal-number', {
      delay: 200,
      distance: '50px',
      duration: 1000,
      easing: 'ease-in-out',
      origin: 'left',
    });

    ScrollReveal().reveal('.reveal-text', {
      delay: 400,
      distance: '50px',
      duration: 1000,
      easing: 'ease-in-out',
      origin: 'right',
    });
  }, []);

  return (
    <Box sx={{ backgroundColor: '#fff', padding: '50px' }}>
      <Grid container spacing={3} alignItems="center">
        {/* Left Side: Large number */}
        <Grid item xs={12} sm={3} className="reveal-number">
          <Typography
            variant="h1"
            component="div"
            sx={{
              fontSize: '80px',
              fontWeight: 'bold',
              color: '#FF4081', // Adjust color to your preference
              fontFamily: 'Quicksand',
            }}
          >
            20+
          </Typography>
        </Grid>

        {/* Right Side: Title and description */}
        <Grid item xs={12} sm={9} className="reveal-text">
          <Typography
            variant="h4"
            component="div"
            sx={{
              fontWeight: 'bold',
              color: '#FF9800', // Title color, similar to the image
              marginBottom: '20px',
              fontFamily: 'Quicksand',
            }}
          >
            THNG IP T NH SNG LP
          </Typography>
          <Typography
            variant="body1"
            component="p"
            sx={{
              fontSize: '16px',
              lineHeight: '1.8',
              color: '#333', // Text color
              fontFamily: 'Quicksand',
            }}
          >
            20 nm pht trin phn mm qun l khm cha bnh cho cc bnh vin ln ti Si Gn v
            cc phng khm a khoa, cng vi hn 17 nm kinh nghim lm bc s ti cc bnh vin u
            ngnh cng lp, cng nh tri qua nhiu v tr quan trng ti cc bnh vin t nhn, phng
            khm a khoa, chng ti thu hiu c cm xc su sc nht ca ngi bnh khi tri nghim
            cc dch v ti cc c s y t. Sc khe ngi bnh gn nh do ngi bnh t qun l, bnh
            nhn phi t xem mnh ang b vn  g v sc khe, cn i bnh vin no khm, cn gp bc
            s no, tt c u do bnh nhn t qun l v quyt nh...
          </Typography>
        </Grid>
      </Grid>
    </Box>
  );
};

export default MessageComponent;
