import React, { useEffect } from "react";
import { Grid, Box, Typography, Button } from "@mui/material";
import ScrollReveal from "scrollreveal"; // Importing ScrollReveal for animations

// Replace this with the actual path to the doctor and patient image
const chronicCareImage = 'https://img.freepik.com/free-vector/flat-national-doctor-s-day-illustration-with-medic-consulting-patient_23-2149430655.jpg?t=st=1728452886~exp=1728456486~hmac=4d5dbbe70a8c095c90d564b9918f2fda8a75a135e909820f47b531c93f61aa96&w=740';

const ChronicCareComponent = () => {
  useEffect(() => {
    // Apply ScrollReveal to the elements
    ScrollReveal().reveal('.reveal', {
      delay: 200,
      distance: '50px',
      duration: 800,
      easing: 'ease-in-out',
      origin: 'bottom',
    });
  }, []);

  return (
    <Box sx={{ backgroundColor: "#fff", padding: "50px" }}>
      <Grid container spacing={3} alignItems="center">
        {/* Left Side: Image */}
        <Grid item xs={12} sm={6} className="reveal">
          <img
            src={chronicCareImage}
            alt="Chronic Care Management"
            style={{ maxWidth: "60%", height: "auto", borderRadius: "10px" }}
          />
        </Grid>

        {/* Right Side: Text */}
        <Grid item xs={12} sm={6} className="reveal">
          <Typography
            variant="h4"
            component="div"
            sx={{
              fontWeight: "bold",
              color: "#FF9800",
              textAlign: "left",
              marginBottom: "20px",
              fontFamily: "Quicksand",
            }}
          >
            QUN L BNH NHN MN TNH
          </Typography>
          <Typography
            variant="h5"
            component="div"
            sx={{
              color: "#333",
              textAlign: "left",
              marginBottom: "40px",
              fontFamily: "Quicksand",
            }}
          >
            Qun l lu di cc bnh v li sng.
          </Typography>
          <Typography
            variant="h6"
            component="div"
            sx={{
              color: "#333",
              textAlign: "left",
              marginBottom: "20px",
              fontFamily: "Quicksand",
            }}
          >
            Nu bn mun gp bc s trc tip hn l qua mng, bn c th thun tin n phng khm v tinh DoctorSaigon gn nht  nhn c y  cc dch v chm sc sc khe ban u, bao gm iu tr cc tnh trng cp tnh v mn tnh  c ngi ln v tr em, khm sc khe v lm vic - sng lc v tim chng lin quan. Vi nhiu a im ca chng ti  Vit Nam, s thun tin s khng thnh vn .
          </Typography>
          <Button variant="contained" color="primary" sx={{ fontFamily: "Quicksand" }}>
            Tm hiu thm
          </Button>
        </Grid>
      </Grid>
    </Box>
  );
};

export default ChronicCareComponent;
