import React, { useEffect } from "react";
import {
  Grid,
  Card,
  CardContent,
  CardMedia,
  Typography,
  Button,
  Box,
} from "@mui/material";
import ScrollReveal from "scrollreveal"; // Import ScrollReveal

// Array with card data (image, title, description)
const cards = [
  {
    image: "https://www.doctosmart.com/img/home5/ppp.png",
    title: "SHPT.HIS",
    description:
      "HIS l vit tt ca H thng thng tin bnh vin. y l mt h thng qun l d liu v h s bnh n ca bnh nhn trong cc c s y t. N gip gim thiu thi gian x l h s v nng cao  chnh xc trong vic lu tr v qun l thng tin bnh nhn.",
  },
  {
    image: "https://www.doctosmart.com/img/home5/ppp.png",
    title: "SHPT.LIS",
    description:
      "LIS l h thng qun l thng tin phng th nghim. N gip cc phng th nghim qun l cc mu, kim tra v x l kt qu ca chng mt cch hiu qu hn.",
  },
  {
    image: "https://www.doctosmart.com/img/home5/ppp.png",
    title: "SHPT.RIS",
    description:
      "RIS l h thng thng tin chn on hnh nh, cho php lu tr v qun l cc hnh nh y khoa mt cch an ton v nhanh chng. iu ny c bit hu ch trong vic theo di tin trnh iu tr ca bnh nhn.",
  },
  {
    image: "https://www.doctosmart.com/img/home5/ppp.png",
    title: "SHPT.PACS",
    description:
      "PACS l h thng lu tr v truyn ti hnh nh y khoa k thut s. N tch hp vi cc h thng y t khc  qun l d liu mt cch hiu qu v ng b ha gia cc chuyn ngnh khc nhau.",
  },
  {
    image: "https://www.doctosmart.com/img/home5/ppp.png",
    title: "SHPT.EMR",
    description:
      "EMR l h s bnh n in t, mt gii php gip cc c s y t d dng lu tr, qun l v truy xut thng tin bnh nhn mt cch tin li v nhanh chng.",
  },
  {
    image: "https://www.doctosmart.com/img/home5/ppp.png",
    title: "SHPT.PMS",
    description:
      "PMS l h thng qun l phng khm, gip cc phng khm t nhn theo di v qun l cc hot ng hng ngy nh tip nhn bnh nhn, lch hn v cc dch v y t.",
  },
];

const CardComponent = () => {
  useEffect(() => {
    // Initialize ScrollReveal and apply it to all cards
    ScrollReveal().reveal(".reveal", {
      delay: 200,
      distance: "50px",
      duration: 800,
      easing: "ease-in-out",
      origin: "bottom",
    });
  }, []);

  return (
    <Box sx={{ padding: "40px" }}>
      <Grid container spacing={3}>
        {cards.map((card, index) => (
          <Grid item xs={12} sm={4} key={index} className="reveal">
            <Card
              sx={{
                boxShadow: "0px 3px 8px 2px rgba(0, 0, 0, 0.15)", // Add shadow effect on hover
                borderRadius: "15px",
                color: "#fff",
                margin: "8px",
                transition: "transform 0.3s ease, box-shadow 0.3s ease", // Add smooth transition
                "&:hover": {
                  transform: "scale(1.05)", // Scale the card when hovered
                  boxShadow: "4px 4px 4px 2px rgba(0, 0, 0, 0.15)", // Add shadow effect on hover
                },
              }}
            >
              <CardMedia
                component="img"
                height="250"
                image={card.image}
                alt={card.title}
                style={{ objectFit: "cover" }}
              />
              <CardContent>
                <Typography
                  gutterBottom
                  variant="h5"
                  component="div"
                  sx={{ color: "#FF4081", fontFamily: "Quicksand" }}
                >
                  {card.title}
                </Typography>
                <Typography
                  variant="body2"
                  color="text.secondary"
                  sx={{
                    display: "-webkit-box",
                    overflow: "hidden",
                    WebkitBoxOrient: "vertical",
                    WebkitLineClamp: 3, // Display up to 3 lines only
                    textOverflow: "ellipsis",
                    color: "black",
                    fontFamily: "Quicksand",
                  }}
                >
                  {card.description}
                </Typography>
              </CardContent>
              <Box sx={{ padding: "20px", textAlign: "center" }}>
                <Button
                  variant="contained"
                  sx={{
                    backgroundColor: "#FF4081",
                    borderRadius: "25px",
                    padding: "10px 30px",
                    color: "#fff",
                    fontFamily: "Quicksand",
                  }}
                >
                  Xem Thm
                </Button>
              </Box>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default CardComponent;
