import React, { useEffect } from "react";
import { Grid, Box, Typography } from "@mui/material";
import { LocalHospital, Lock } from "@mui/icons-material"; // Icons
import ScrollReveal from "scrollreveal"; // Import ScrollReveal

// Replace this with the actual path to the video call image
const videoCallImage =
  "https://img.freepik.com/free-vector/online-doctor-with-stethoscope_23-2148519113.jpg?t=st=1728451708~exp=1728455308~hmac=e25a83c7f43e59adba518488a6f40946fb2410af39847283e5b27bef3cf52ac4&w=740";

const DoctorSaigonSystemComponent = () => {
  useEffect(() => {
    // Apply ScrollReveal animations
    ScrollReveal().reveal(".reveal-image", {
      delay: 200,
      distance: "50px",
      duration: 1000,
      easing: "ease-in-out",
      origin: "left",
    });

    ScrollReveal().reveal(".reveal-text", {
      delay: 400,
      distance: "50px",
      duration: 1000,
      easing: "ease-in-out",
      origin: "right",
    });
  }, []);

  return (
    <Box sx={{ backgroundColor: "#fff", padding: "50px" }}>
      {/* Title Section */}
      <Grid container spacing={3} alignItems="center">
        {/* Left Side: Image */}
        <Grid item xs={12} sm={6} className="reveal-image">
          <img
            src={videoCallImage}
            alt="Video Call Consultation"
            style={{ maxWidth: "80%", height: "auto", borderRadius: "10px" }}
          />
        </Grid>

        {/* Right Side: Text */}
        <Grid item xs={12} sm={6} className="reveal-text">
          <Typography
            variant="h4"
            component="div"
            sx={{
              fontWeight: "bold",
              color: "#FF9800",
              textAlign: "left",
              marginBottom: "20px",
              fontFamily: "Quicksand",
            }}
          >
            200+ H THNG PHNG KHM V TINH DoctorSaigon
          </Typography>
          <Typography
            variant="h5"
            component="div"
            sx={{
              color: "#333",
              textAlign: "left",
              marginBottom: "40px",
              fontFamily: "Quicksand",
            }}
          >
            Tn dng cng ngh v kt ni vi bc s chuyn mn m khng cn n
            bnh vin.
          </Typography>
          <Typography
            variant="h6"
            component="div"
            sx={{
              color: "#333",
              textAlign: "left",
              marginBottom: "40px",
              fontFamily: "Quicksand",
            }}
          >
            Nu bn mun gp bc s trc tip hn l qua mng, bn c th thun
            tin n phng khm v tinh DoctorSaigon gn nht  nhn c y
             cc dch v chm sc sc khe ban u, bao gm iu tr cc tnh
            trng cp tnh v mn tnh  c ngi ln v tr em, khm sc khe
            v lm vic- sng lc v tim chng lin quan. Vi nhiu a im
            ca chng ti  Vit Nam, s thun tin s khng thnh vn .
          </Typography>
          
        </Grid>
      </Grid>
    </Box>
  );
};

export default DoctorSaigonSystemComponent;
