import React from 'react';
import { Grid, Box, Typography } from '@mui/material';
import { Facebook, Instagram, LinkedIn, Twitter } from '@mui/icons-material';

const MyFooter = () => {
  return (
    <Box sx={{ backgroundColor: '#0060CE', padding: '40px 20px', color: '#fff' }}>
      <Grid container spacing={3} justifyContent="space-between">
        {/* Contact Section */}
        <Grid item xs={12} sm={4}>
          <Typography variant="h6" sx={{ fontWeight: 'bold', marginBottom: '15px' }}>
            Lin h:
          </Typography>
          <Typography variant="body2">
            S 29, ng 218 Cao L, P4, Qun 8, TP.H Ch Minh
          </Typography>
          <Typography variant="body2" sx={{ margin: '5px 0' }}>
            0914 135 469 <br />
            0947 800 404 <br />
            0917 471 080
          </Typography>
          <Typography variant="body2">
            cskh@shpt.com.vn <br />
            info@shpt.com.vn
          </Typography>
        </Grid>

        {/* Social Media Section */}
        <Grid item xs={12} sm={4} textAlign="center">
          <Typography variant="h6" sx={{ fontWeight: 'bold', marginBottom: '15px' }}>
            Theo di chng ti:
          </Typography>
          <Box sx={{ display: 'flex', justifyContent: 'center', gap: '20px' }}>
            <Facebook sx={{ fontSize: 40, color: '#FF4081' }} />
            <Instagram sx={{ fontSize: 40, color: '#FF4081' }} />
            <LinkedIn sx={{ fontSize: 40, color: '#FF4081' }} />
            <Twitter sx={{ fontSize: 40, color: '#FF4081' }} />
          </Box>
        </Grid>

        {/* Policies Section */}
        <Grid item xs={12} sm={4}>
          <Typography variant="h6" sx={{ fontWeight: 'bold', marginBottom: '15px' }}>
            Chnh sch:
          </Typography>
          <Typography variant="body2" sx={{ marginBottom: '10px' }}>
            Quy trnh trin khai
          </Typography>
          <Typography variant="body2" sx={{ marginBottom: '10px' }}>
            Hnh thc thanh ton
          </Typography>
          <Typography variant="body2">
            Bo tr/Bo hnh
          </Typography>
        </Grid>
      </Grid>
    </Box>
  );
};

export default MyFooter;
